export default function (card) {
  //    
  let checksum = 0;
  //        
  const cardnumbers = card.split('').map(Number);

  //    
  for (const [index, num] of cardnumbers.entries()) {
    //  index ,      
    //       
    if (index % 2 === 0) {
      const buffer = num * 2;
      //     9,     9     
      //  ,      
      buffer > 9 ? checksum += buffer - 9 : checksum += buffer;
    } else {
      checksum += num;
    }
  }
  //        10,    
  return checksum % 10 === 0;
}
